import { v4 as uuidv4 } from 'uuid';
import { useState, useRef } from 'react';
import './Facts.scss';

const Facts = ({ facts }) => {
  const [isShort, setIsShort] = useState(true);

  const btnRef = useRef(null);

  function handleMoreFacts() {
    setIsShort(!isShort);
    if (btnRef.current.textContent === ' ') {
      btnRef.current.textContent = '';
    } else {
      btnRef.current.textContent = ' ';
    }
  }
  return (
    <section className='more__facts facts'>
      <h2 className='facts__title'>  , ...</h2>
      {facts.every((fact) => typeof fact === 'string') ? (
        <ul className={`facts__list ${isShort ? 'facts__list--short' : ''}`}>
          {facts.map((fact) => (
            <li className='facts__list-item' key={uuidv4()}>
              {fact}
            </li>
          ))}
        </ul>
      ) : (
        <ul className={`facts__list ${isShort ? 'facts__list--short' : ''}`}>
          {facts.map((fact) => (
            <li
              className='facts__list-item'
              key={uuidv4()}
              dangerouslySetInnerHTML={{ __html: fact.text }}></li>
          ))}
        </ul>
      )}
      {facts.length > 3 && (
        <button
          type='button'
          className={`facts__btn ${isShort ? '' : 'facts__btn--rotated'}`}
          onClick={handleMoreFacts}
          ref={btnRef}>
           
        </button>
      )}
    </section>
  );
};

export default Facts;
