export const getBoxOfficeType = (boxOffice) => {
    switch (boxOffice.type) {
        case 'BUDGET':
            return '';
        case 'USA':
            return '  ';
        case 'MARKETING':
            return '';
        case 'WORLD':
            return '  ';
        case 'RUS':
            return '  ';
        default:
            break;
    }
};